import React, { useState,useEffect } from 'react';

import { Pagination,EffectFade,Autoplay} from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/effect-fade';

import project from './project.json';

//fade        active   
const MokupSlider = ({item}) => {
  //     item.id index+1 dl   
  //          
  //  findmokupImg[0]  map  
  const mokupImgArr = project.projects.map((aa) => aa.detailMokupImg); 
  const findmokupImg = mokupImgArr.find((value,index)=> item.id == index +1); //array 1,


  //const [currentIndex, setCurrentIndex] = useState(0)


  return (
    <Swiper
    modules={[Pagination,EffectFade,Autoplay]}
    spaceBetween={50}
    slidesPerView={1}
    loop
    pagination={{ clickable: true }}
    effect='fade'
    autoplay={{ delay: 3000 }}
        >
      {findmokupImg.map((el,index)=>
        (<SwiperSlide key={index}>
          <img src={el} alt="" />
        </SwiperSlide>)
      )}
    </Swiper>
  )
}

export default MokupSlider;

