import React,{ useEffect } from 'react';
import { motion } from 'framer-motion';

const ModalAnswer04 = ({variants,controls,bubble}) => {
  useEffect(()=>{
    controls.start('visible');
  },[controls]); 
  return (
    <motion.div 
    variants={variants}
    initial="hidden"
    animate={controls}>
    <div className='bubble_box right'>
      <motion.div 
        className='bubble_text'
        variants={bubble}>
           !
      </motion.div>
    </div>
    <div className='bubble_box'>
          <motion.div 
            className='bubble_text'
            variants={bubble}
            >
            
          </motion.div>
        </div>
        <div className='bubble_box'>
          <motion.div 
            className='bubble_text'
            variants={bubble}
            >
                 !
          </motion.div>
        </div>
        <div className='bubble_box'>
          <motion.div 
            className='bubble_text'
            variants={bubble}
            >
              <br/>
            <a href="mailto:gkdlt7373@gmail.com" target="_blank" rel="noopener noreferrer"> gkdlt7373@gmail.com</a>
            
          </motion.div>
        </div>
  </motion.div>
  )
}

export default ModalAnswer04